import React, { Component } from "react";
import head from "../assets/img/headPic.jpg";
import {
  HomeFilled,
  FileFilled,
  ReadFilled,
  UnorderedListOutlined,
} from "@ant-design/icons";
import BackgroundChange from "../components/backgroundChange";

class aside extends Component {
  state = {
    navTopFlag: true,
    innerStyle: {
      background: "#5FCB71",
    },
    backgroundColor: [
      "#5FCB71",
      "#5BC3D5",
      "#3B7EEB",
      "#5ECCA9",
      "#EEA73B",
      "#5469C9",
      "#5D6BA7",
      "#6C51A4",
    ],
    nav: [
      {
        icon: <HomeFilled />,
        name: "Blog Home",
        href: "/",
      },
      {
        icon: <FileFilled />,
        name: "Blog Post",
        href: "/blog",
      },
      {
        icon: <ReadFilled />,
        name: "About Me",
        href: "/about",
      },
    ],
  };
  handleClick() {
    window.location.href = "/about";
  }

  renderNav() {
    const nav = this.state.nav;
    return nav.map((item) => (
      <li key={item.name}>
        {item.icon}
        <a href={item.href}>{item.name}</a>
      </li>
    ));
  }

  changeColor = (msg) => {
    let color = Object.assign({}, this.state.nowBackgroundColor, {
      background: msg,
    });
    this.setState({
      nowBackgroundColor: color,
    });
  };

  handleTopNav(navTopFlag) {
    if (navTopFlag) {
      document.getElementsByClassName("aside_auto")[0].style.width = "100%";
      document.getElementsByClassName("topNav")[0].style.color = "#fff";
    } else {
      document.getElementsByClassName("aside_auto")[0].style.width = "0%";
      document.getElementsByClassName("topNav")[0].style.color = "#000";
    }
    this.setState({
      navTopFlag: !navTopFlag,
    });
  }
  render() {
    let { backgroundColor, innerStyle, navTopFlag } = this.state;
    return (
      <div className="asideOutWidth">
        <div style={innerStyle} className="aside_auto">
          <div className="aside">
            <h2>Psp's Blog</h2>
            <div className="head">
              <img src={head} alt="head" />
            </div>
            <p className="desc_me">
              Hi, my name is Psp. Briefly introduce yourself here. You can also
              provide a link to the about page.
              <br />
              <a href="/about">Find out more about me</a>
            </p>
            <div className="aside_bottom">
              <ul>{this.renderNav()}</ul>
            </div>
            <button className="getTouch" onClick={() => this.handleClick()}>
              Get in Touch
            </button>
          </div>
        </div>
        <div className="topNav">
          <UnorderedListOutlined
            onClick={() => this.handleTopNav(navTopFlag)}
          />
        </div>
        {/* <BackgroundChange
          backgroundColor={backgroundColor}
          changeColor={this.changeColor}
        ></BackgroundChange> */}
      </div>
    );
  }
}

export default aside;
