import React, { Component } from "react";
import { Input } from "antd";
import { Row, Col } from "antd";
import Footer from "./footer.js";
import img1 from "../assets/img/blog-post-thumb-1.jpg";
import img2 from "../assets/img/blog-post-thumb-2.jpg";
import img3 from "../assets/img/blog-post-thumb-3.jpg";
import img4 from "../assets/img/blog-post-thumb-4.jpg";
import img5 from "../assets/img/blog-post-thumb-5.jpg";
import img6 from "../assets/img/blog-post-thumb-6.jpg";
const { Search } = Input;
class publicBlog extends Component {
  state = {
    blogs: [
      {
        imgUrl: img1,
        title: "Why Every Developer Should Have A Blog",
        comments: "Published 2 days ago5 min read8 comments",
        content:
          "Lorem ipsum dolor sit amet, consectetuer adipiscing elit. Aenean commodo ligula eget dolor. Aenean massa. Cum sociis natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus. Donec quam felis, ultricies...",
        href: "/blog",
      },
      {
        imgUrl: img2,
        title: "A Guide to Becoming a Full-Stack Developer",
        comments: "Published 3 months ago3 min read26 comments",
        content:
          "Lorem ipsum dolor sit amet, consectetuer adipiscing elit. Aenean commodo ligula eget dolor. Aenean massa. Cum sociis natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus. Donec quam felis, ultricies...",
        href: "/blog",
      },
      {
        imgUrl: img3,
        title: "High Performance JavaScript",
        comments: "Published 1 month ago8 min read12 comments",
        content:
          "Lorem ipsum dolor sit amet, consectetuer adipiscing elit. Aenean commodo ligula eget dolor. Aenean massa. Cum sociis natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus. Donec quam felis, ultricies...",
        href: "/blog",
      },
      {
        imgUrl: img4,
        title: "Top 5 JavaScript Frameworks",
        comments: "Published 2 months ago15 min read3 comments",
        content:
          "Lorem ipsum dolor sit amet, consectetuer adipiscing elit. Aenean commodo ligula eget dolor. Aenean massa. Cum sociis natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus. Donec quam felis, ultricies...",
        href: "/blog",
      },
      {
        imgUrl: img5,
        title: "Learn React in 24 Hours",
        comments: "Published 2 months ago10 min read23 comments",
        content:
          "Lorem ipsum dolor sit amet, consectetuer adipiscing elit. Aenean commodo ligula eget dolor. Aenean massa. Cum sociis natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus. Donec quam felis, ultricies...",
        href: "/blog",
      },
      {
        imgUrl: img6,
        title: "About Remote Working",
        comments: "Published 3 months ago2 min read1 comment",
        content:
          "Lorem ipsum dolor sit amet, consectetuer adipiscing elit. Aenean commodo ligula eget dolor. Aenean massa. Cum sociis natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus. Donec quam felis, ultricies...",
        href: "/blog",
      },
    ],
  };

  renderBlogList() {
    const blogs = this.state.blogs;
    return blogs.map((item) => (
      <Col xl={24} key={item.title}>
        <div className="list_item">
          <div className="blog_img">
            <img src={item.imgUrl} alt="img" />
          </div>
          <div className="blog_right">
            <h2>{item.title}</h2>
            <p className="comments">{item.comments}</p>
            <p className="content">{item.content}</p>
            <a href={item.href}>Read more →</a>
          </div>
        </div>
      </Col>
    ));
  }

  render() {
    return (
      <div>
        <div className="publicBlog">
          <div className="publicBlog_outer">
            <h1>DevBlog - A Blog Template Made For Developers</h1>
            <p>
              Welcome to my blog. Subscribe and get my latest blog post in your
              inbox.
            </p>
            <Search
              className="input_search"
              placeholder="input search text"
              allowClear
              enterButton="Search"
              size="large"
            />
          </div>
        </div>
        <div className="container_blog">
          <Row className="publicBlog_col">{this.renderBlogList()}</Row>
        </div>
        {/* <Footer></Footer> */}
      </div>
    );
  }
}

export default publicBlog;
