import React, { Component } from "react";
import { SettingFilled, CloseCircleFilled } from "@ant-design/icons";

class backgroundChange extends Component {
  state = {
    colorHidden: true,
  };

  openChoose() {
    const { colorHidden } = this.state;
    this.setState({
      colorHidden: !colorHidden,
    });
  }

  changeColor(msg) {
    this.props.changeColor(msg);
  }

  renderColorList() {
    const backgroundColor = this.props.backgroundColor;
    return backgroundColor.map((item) => (
      <li key={item}>
        <button
          className="choose_btn"
          style={{ background: item }}
          onClick={() => this.changeColor(item)}
        ></button>
      </li>
    ));
  }

  render() {
    const { colorHidden } = this.state;
    let translateX = {
      transform: colorHidden ? "translateX(100%)" : "translateX(0%)",
    };
    return (
      <div className="backgroundChange" style={translateX}>
        <SettingFilled className="icon" onClick={() => this.openChoose()} />
        <div>
          <span>CHOOSE COLOUR</span>
          <CloseCircleFilled
            className="close_choose"
            onClick={() => this.openChoose()}
          />
          <ul className="choose_bg">
            {this.renderColorList()}
          </ul>
        </div>
      </div>
    );
  }
}

export default backgroundChange;
